import { FC, useEffect, useState } from "react";
import { Image, Space, Typography, type ImageProps } from "antd";
import { SpaceImagesResponse, SpaceImageItem } from "../../types/imageTypes";
import { MAX_CONTENT_WIDTH } from "../../../constants";

const { Text } = Typography;

type Props = {
  width?: number | string;
  preview?: ImageProps["preview"];
  imagesData?: SpaceImagesResponse;
};

export const ImageWithDescription: FC<Props> = ({
  width,
  preview = true,
  imagesData,
}) => {
  const imagesUrls = imagesData?.map((item) => `${item.url}`);
  const [currentImage, setCurrentImage] = useState<SpaceImageItem | null>(null);

  useEffect(() => {
    if (imagesData?.length) {
      setCurrentImage(imagesData[0]);
    }
  }, [imagesData]);

  return (
    <Space direction="vertical" size="small" wrap>
      <Image.PreviewGroup
        preview={{
          onChange: (current) => {
            if (imagesData && current) {
              setCurrentImage(imagesData[current]);
            }
          },
        }}
        items={imagesUrls}
      >
        <Image
          width={width}
          src={currentImage?.url}
          alt="albom-image"
          preview={preview}
          style={{ borderRadius: 12 }}
          fallback="data:image/png;base64,"
          placeholder
        />
      </Image.PreviewGroup>

      <Space
        direction="vertical"
        size={0}
        style={{ maxWidth: MAX_CONTENT_WIDTH }}
      >
        <Text ellipsis>{currentImage?.title}</Text>
        <Text type="secondary" ellipsis>
          {currentImage?.copyright}
        </Text>
      </Space>
    </Space>
  );
};
